.assign-capabilities-group select {
  min-height: 300px;
  overflow: scroll;
}
.assign-capabilities-group select > option {
  position: relative;
  margin-bottom: 4px;
}
.capability-select-deselect {
  margin-top: 120px;
}
.capability-select-deselect > div {
  cursor: pointer;
  text-align: center;
}
.capability-select-deselect > div:hover {
  outline: 1px solid #8a8c9c;
  background-color: #8a8c9c;
  color: white;
}
